<template>
  <div id="app">
    <img src="./assets/logo.png" alt="Clement Labs" id="logo" @click="switchRoute({name: 'home'})">
    <div @click="showMenu = !showMenu" class="icon" :class="{active: showMenu}">
      <div class="hamburger"></div>
    </div>
    <div id="menu" :class="{showMenu: showMenu, hideMenu: !showMenu}" class="loader-menu-common">
      <div class="routerLink" @click="switchRoute({name: 'home'})">
        <h3>Home</h3>
        <div class="underline"></div>
      </div>
      <div class="routerLink" @click="switchRoute({name: 'services'})">
        <h3>Services</h3>
        <div class="underline"></div>
      </div>
      <div class="routerLink" @click="switchRoute({name: 'about'})">
        <h3>About</h3>
        <div class="underline"></div>
      </div>
      <div class="routerLink" @click="switchRoute({name: 'portfolio'})">
        <h3>Portfolio</h3>
        <div class="underline"></div>
      </div>
      <div class="routerLink" @click="switchRoute({name: 'contact'})">
        <h3>Contact us</h3>
        <div class="underline"></div>
      </div>
    </div>
    <div id="loader" :class="{showMenu: showLoader, hideMenu: !showLoader}" class="loader-menu-common">
      <lottie :options="loaderOptions" :height="250" :width="250" @animCreated="handleAnimation"/>
    </div>
    <router-view/>
  </div>
</template>

<script>
import Lottie from 'vue-lottie';
import * as loaderAnimationData from './assets/loaderAnimationData.json';
import { setTimeout } from 'timers';

export default {
  components: {
    'lottie': Lottie
  },

  mounted() {
    this.anim.play()
    setTimeout(() => {
      this.showLoader = false
    }, 2500)
    setTimeout(() => {
      this.anim.stop()
    }, 3500)
  },

  methods: {
    switchRoute(route) {
      this.showLoader = true
      this.anim.play()
      this.$router.push(route)
      setTimeout(() => {
        this.showLoader = false
        this.showMenu = false
      }, 2500)
      setTimeout(() => {
        this.anim.stop()
      }, 3500)
    },

    handleAnimation: function (anim) {
      this.anim = anim;
    },

    stop() {
      this.anim.stop();
    }
  },

  data() {
    return {
      loaderOptions: {animationData: loaderAnimationData.default, loop: false, autoplay: false},
      showMenu: false,
      showLoader: true
    }
  }
}
</script>


